import { ValidatorFn } from './types'
import { isString, lodashGet, isEmpty } from './lodash'
/**
 * TODO 
 */
export const validator: Record<string, ValidatorFn> = {
  'password' (v: string, othername: string, { data }) {
    if (isString(v) && isString(othername)) {
      return v === lodashGet(data, othername)
    }
    return ``
  },
  'required' (v: any, msg: string) {
    return !isEmpty(v) ? true : (msg || '')
  },
  'email' (v: any, msg: string) {
    return isString(v) && /^([A-Za-z0-9\_\-\.])+@([A-Za-z0-9\_\-\.])+\.([A-Za-z]{2,4})$/.test(v) ? true : (msg || 'Email')
  },
  'ipv4' (v: any, msg: string) {
    return isString(v) && /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?).){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(v) ? true : (msg || 'IPV4')
  },
  'url' (v: any, msg: string) {
    return isString(v) && /^((https?|ftp|file):\/\/)?([da-z.-]+).([a-z.]{2,6})([/w .-]*)*\/?$/.test(v) ? true : (msg || 'url')
  },
  'equals' (v: string, o: string) {
    return v === o ? true : `${v}${o}`
  },
  'username' (v: any, msg: string) {
    return isString(v) && /^[a-zA-Z0-9_-]{4,16}$/.test(v) ? true : (msg || '416_-')
  },
  'phone' (v: any, msg: string) {
    return isString(v) && /^1[3456789]d{9}$/.test(v) ? true : (msg || '')
  },
  'cn' (v: any, msg: string) {
    return isString(v) && /^([u4E00-u9FA5])+$/.test(v) ? true : (msg || '')
  }
}